.filter {

    width: 100%;
    max-width: 1000px;
    background-color: #3F3F3F;
    display: flex;
    padding: 50px;
    flex-direction: column;
    align-items: center;
}

.filter__top{
    width: 100%;
    display: flex;
    justify-content: center;
}
.filter__top_inp{
    width: 100%;
    border:none;
    max-width: 500px;
    height: 45px;
    font-size: 1.4rem;
    outline: none;
}
.filter__bottom{
    align-items: center;
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 3rem;
}
@media  screen and (max-width:900px) {
    .filter{
        padding: 50px 2px;
    }
    .filter__top_inp{
        width: 95%;
    }
    .filter__bottom{
        flex-direction: column;
        align-items:unset;
        justify-content: center;
    }
    .date-filter{
        width: 100%;
        display: flex;
        height: 60px;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        padding-bottom: 10px;
    }
    .date-filter input{
        width: 35% !important;
        max-width: unset;
    }
    .date-filter label{
        padding: 0;
    }
    .filter__bottom  select{
        margin:10px 10px;    

    }
    .filter__bottom label{

        margin:2px 10px;    
        
    }
    .search__btn{

        width: 80% !important;
    }
    
}
.filter__bottom select{
    min-width: 50px;
    height: 40px;
    font-size: 1.2rem;
}
.filter__bottom input{
    width: 90px;
    height: 40px;
    font-size: 1.2rem;
}
.filter__bottom label{
    font-size: 1.2rem; 
    color: white;
}
.search__btn{
    width: 150px;
    height: 40px;
    color:#3F3F3F;
    background-color:white ;
    border:none;
    outline: none;
    cursor: pointer;
    font-size: 1.5rem;
    margin-top:50px;

    transition: all 0.4s ease;
}
.search__btn:hover{
    background-color: #3F3F3F;
    color:white;

    box-shadow: 0px 0px 4px 2px white;
}